    import uniList from '.../../../components/uni-list/uni-list.vue'
    import uniListItem from '../../../components/uni-list-item/uni-list-item.vue'
    import uniTag from "../../../components/uni-tag/uni-tag.vue" //标签组件
	import BjxInputs from '../../../components/bjx-inputs/bjx-inputs.vue'//远程搜索输入
	export default {
        data() {
            return {
                stars: [1, 2, 3, 4, 5],
                imageList: [],
                sendDate: {
                    score: 0,
                    content: "",
                    contact: ""
                },
				dataZhengxing: [
					{id: 1, value: '胃热滞脾'},
					{id: 2, value: '脾虚不运'},
					{id: 3, value: '脾肾阳虚'},
					{id: 4, value: '痰湿淤阻'},
					{id: 5, value: '气滞血瘀'}
				],
				dataZhongyibingming: [
					{id: 1, value: '肥胖病'},
					{id: 2, value: '伤筋病'},
					{id: 3, value: '眩晕病'},
					{id: 4, value: '不孕病'},
					{id: 5, value: '痰饮病'}
				]
            }
        },
        onLoad() {
            
        },
        methods: {
            close(e){
                this.imageList.splice(e,1);
            },
            //页面跳转
            gotoHuanzhexinxi(){
            	uni.navigateTo({
            	url: '../../room/huanzhexinxi/huanzhexinxi'
            	});
            },
			// 远程加载数据 返回一个对象，{value: 输入框值, callback: 回调函数}
			getDataZhengxing(e) {
				console.log(e)
				if(!e.value){
					e.callback(this.dataZhengxing)
				}else{
					let data = []
					this.dataZhengxing.forEach(item=>{
						if(item.indexOf(e.value) > -1){
							data.push(item)
						}
					})
					e.callback(data)
				}
			},
			getData2(e) {
				console.log(e)
				this.value = e.value
				if(!e.value){
					e.callback(this.data2)
				}else{
					let data = []
					this.data2.forEach(item=>{
						if(item.value.indexOf(e.value) > -1){
							data.push(item)
						}
					})
					e.callback(data)
				}
			},
			select1(value){
				console.log(1,value)
			},
			select2(value){
				this.value = value.value
				console.log(2,value)
			},
			// 若要在父组件动态改变bjx-inputs组件中的值 请直接调用组件中的setValue方法
			setInputsValue(){
				this.$refs.inputs1.setValue(this.value)
			},
			focus1(e){
